@import "nav";
@import "section";

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	scroll-behavior: smooth;
}

main {
	font: {
		size: $fontSizeP;
		color: $fgColor;
	}
	background: {
		color: $bgColor;
	}
}

canvas {
	width: 100%;
  height: 100%;
  border: 0;
  overflow: hidden;
  display: block;
}

@include setHeaderFontSizes($fontSizeH, $fontSizeP);
